


body {
  background-image: url(../img/background.6952de7e.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed; 
  background-position:top;
  background-size: cover;
  text-align: center;


  color: #e7e4e4;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.697);

}
@font-face {
  font-family: GraublauWeb;
  font-weight: bold;
  src: url("./domino_jack/dominojack3dital.otf") format("opentype");
}
@font-face {
  font-family: Toxico;
  font-weight: bold;
  src: url("./toxico/Toxico.otf") format("opentype");
}
@font-face {
  font-family: lobelo;
  src: url(./toxico//LeagueSpartan-Bold.otf) format("opentype");
}
@font-face {
  font-family: batman;
  font-weight: bold;
  src: url("./toxico/batmfa__.ttf") format("truetype");
}
h1,h2,h5{
  font-family:lobelo ;
}
h4{
  font-family:lobelo ;
}

p{
  font-family: lobelo;
}
.btn{
  font-family: lobelo;
  width: auto;
  text-transform: capitalize;
}
