/* border:5px solid rgb(236, 11, 124); */
body{
    z-index: -1;
}
.font-size{
    font-size: 50px;
    font-weight: 900;
    /* font-family: GraublauWeb; */
    margin:70px 0px;
    /* text-shadow: 1px 1px rgb(255, 0, 119); */
    
}

.text-size{
    font-size: 18px;
}
.badge{
    background-color: rgba(255, 0, 128);
}
.btn{
    border: none;
    color: rgb(255, 255, 255);
    transition: 500ms;
   
}
.btn1{
    background-color:rgba(255, 0, 128, 0.9) ;
}
.btn1:hover{
    background-color: black;
    color: white;
}

.btn2{
    background-color: rgba(0, 0, 0, 0.9);
}
.btn2:hover{
    background-color:rgb(236, 11, 124) ;
    color: white;
}
@media screen and (max-width:576px){
    .font-size{
        font-size: 40px;
        font-weight: 800;
        margin:40px 0px;
    }
    
    
}